.user-books {
    padding: 20px;
    max-width: 800px;
    margin: auto;
    background-color: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  }
  
  .user-books h1 {
    text-align: center;
    color: #333;
    margin-bottom: 20px;
  }
  
  .user-books ul {
    list-style-type: none;
    padding: 0;
  }
  
  .user-books li {
    padding: 15px;
    margin-bottom: 15px;
    background-color: #fff;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
  
  .user-books h3 {
    margin: 0;
    color: #2c3e50;
  }
  
  .user-books p {
    margin: 5px 0;
    color: #7f8c8d;
  }
  
  .user-books p:nth-of-type(1) {
    font-weight: bold;
    color: #2980b9;
  }
  